<template>
    <div>
        <a-row class="banner">
            <a-col :span="colsDisplay.contentLeft" class="content-left">
                <div>
                    <a-card>
                        
                        <template #cover>
                        <video disableremoteplayback autoplay muted loop playsinline style="width:100%">
                            <source src="../assets/video/banner2.mp4" type="video/mp4">
                        </video>
                        </template>
                    </a-card>
                </div>
                <div class="title">
                    <img src="../assets/logo.png">
                    <h4>Kembali menilik data, menuju satu dalam Jakarta.</h4>
                </div>
            </a-col>
            <a-col :span="colsDisplay.contentRight" class="content-right">
                <a-row>
                    <a-col :span="24">
                        <h4>STATISTIK</h4>
                        <p>Portal website resmi Unit Pengelola Statistik (UPS) untuk melihat hasil pengelolaan dan pelayanan statistik terkait Provinsi DKI Jakarta. Simak data, angka, hasil survei, dan artikel terbaru tentang Statistik Jakarta disini!</p>
                    </a-col>
                    <a-col :span="24">
                        <a href="https://statistik.jakarta.go.id"><a-button type="primary">KUNJUNGI WEBSITE</a-button></a>
                    </a-col>
                    <a-col :span="24">
                        <router-link to="/"><a-button type="primary">SATU DATA JAKARTA</a-button></router-link>
                    </a-col>
                </a-row>

            </a-col>
        </a-row>
    </div>
</template>

<script>
import axios from 'axios'
import * as cheerio from 'cheerio';
export default {
    data(){
        return{
            colsDisplay:{
                contentLeft:'0',
                contentRight:'0'
            },
            scraping:null
        }
    },
    mounted(){
        if(window.screen.availWidth<='820'){
            this.colsDisplay.contentLeft = '24'
            this.colsDisplay.contentRight = '24'
        } else {
            this.colsDisplay.contentLeft = '16'
            this.colsDisplay.contentRight = '8'
        }

        this.getWebsiteData()

    },
    methods:{
        myEventHandler(e) {
            if(e.srcElement.innerWidth<='820'){
                this.colsDisplay.contentLeft = '24'
                this.colsDisplay.contentRight = '24'
            } else {
                this.colsDisplay.contentLeft = '16'
                this.colsDisplay.contentRight = '8'
            }
        },
        getWebsiteData(){
            let self = this;
            let url = 'https://google.com';
            let dataArray = []
            axios({
                method: 'get',
                url: url,
            })
            .then(function (response) {
                let html = response.data
                let $ = cheerio.load(html)
                $("ul.tertiary li").each(function(){
                        const title = $(this).find('h3').attr('aria-label');
                        const image  = $(this).find('img').attr('src');
                        dataArray.push({
                        'title': title,
                        'image': image
                        })
                });
                self.lastestArticles = dataArray
                console.log(dataArray)
            });
        }
    },
    created() {
        window.addEventListener("resize", this.myEventHandler);
    }
}
</script>