<template>
    <div>
        <a-row class="banner">
            <a-col :span="colsDisplay.contentLeft" class="content-left">
                <div>
                    <a-card>
                        
                        <template #cover>
                        <video disableremoteplayback autoplay muted loop playsinline style="width:100%">
                            <source src="../assets/video/banner2.mp4" type="video/mp4">
                        </video>
                        </template>
                    </a-card>
                </div>
                <div class="title">
                    <img src="../assets/logo.png">
                    <h4>Kembali menilik data, menuju satu dalam Jakarta.</h4>
                </div>
            </a-col>
            <a-col :span="colsDisplay.contentRight" class="content-right">
                <a-row>
                    <a-col :span="24">
                        <h4>OPEN DATA</h4>
                        <p>Publik berhak mengetahui seluruh data pemerintah. untuk itu, Pemprov DKI Jakarta menyajikan data yang lengkap, terbaru, dan transparan. Siapa saja bisa mengakses seluruh data yang ada di portal ini, lengkap dengan infografisnya!</p>
                    </a-col>
                    <a-col :span="24">
                        <a href="https://data.jakarta.go.id"><a-button type="primary">KUNJUNGI WEBSITE</a-button></a>
                    </a-col>
                    <a-col :span="24">
                        <router-link to="/"><a-button type="primary">SATU DATA JAKARTA</a-button></router-link>
                    </a-col>
                </a-row>
                
            </a-col>
        </a-row>
    </div>
</template>

<script>
export default {
    data(){
        return{
            colsDisplay:{
                contentLeft:'0',
                contentRight:'0'
                
            }
        }
    },
    mounted(){
        if(window.screen.availWidth<='820'){
            this.colsDisplay.contentLeft = '24'
            this.colsDisplay.contentRight = '24'
        } else {
            this.colsDisplay.contentLeft = '16'
            this.colsDisplay.contentRight = '8'
        }

    },
    methods:{
        myEventHandler(e) {
            if(e.srcElement.innerWidth<='820'){
                this.colsDisplay.contentLeft = '24'
                this.colsDisplay.contentRight = '24'
            } else {
                this.colsDisplay.contentLeft = '16'
                this.colsDisplay.contentRight = '8'
            }
        }
    },
    created() {
        window.addEventListener("resize", this.myEventHandler);
    }
}
</script>