import { createWebHistory, createRouter } from "vue-router";

import Home from "../pages/Home";
import StatistikJakarta from "../pages/StatistikJakarta";
import OpenData from "../pages/OpenData";
import JakartaSatu from "../pages/JakartaSatu";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/statistik-jakarta",
    name: "StatistikJakarta",
    component: StatistikJakarta,
  },
  {
    path: "/open-data",
    name: "OpenData",
    component: OpenData,
  },
  {
    path: "/jakarta-satu",
    name: "JakartaSatu",
    component: JakartaSatu,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;